









































































































































import Vue from "vue";

export default Vue.extend({
    name: "RomaActivityModalForm",
    props: {
        value: {
            type: Boolean,
            required: true,
        },
        maxWidth: {
            type: String,
            default: '1024',
        },
        overlayOpacity: {
            type: String,
            default: '0.8',
        },
        persistent: {
            type: Boolean,
            default: true,
        },
        unit: {
            type: Object,
            required: true,
        },
        selected: {
            type: Array,
            default () {
                return []
            }
        }
    },
    data() {
        return {
            valid: false,
            loading: false,
            showing: this.value,
            duas: [],
            evaluationTypes: [],
            instrumentTypes: [],
            request: {
                selectedOAs: this.selectedOAs,
                activityLer: '',
                files: [],
                selectedDuas: [],
                activityEva: {
                    type: null,
                    instrument: null,
                    text: '',
                }
            },
        };
    },
    computed: {
        activityRules (value) {
            return [
                value => value.length >= 5 || 'La actividad no puede ser menor a 5 caracteres',
                value => value.length <= 100 || 'La actividad no puede ser mayor a 100 caracteres',
            ]
        }
    },
    watch: {
        value (value) {
          if (value)  this.request.selectedOAs = this.selected;
        },
        selected (value) {
            this.request.selectedOAs = value;
        }
    },
    methods: {
        show (value: Boolean) {
            this.$emit('input', value);
        }
    },
});
