



















































































































































































import Vue from "vue";
import { Ripple } from 'vuetify/lib/directives/';
// import { createSimpleTransition } from 'vuetify/src/util/helpers';
import { mask } from 'vue-the-mask';

import RomaCoverage from "@/components/RomaCoverage.vue";
import RomaOaModalForm from "@/components/RomaOaModalForm.vue";
import RomaConfirmDialog from '@/components/partial/RomaConfirmDialog.vue';
import RomaActivityModalForm from "@/components/RomaActivityModalForm.vue";

// const romaOAListTransition = createSimpleTransition('oa-list-transition');
// Vue.component('my-transition', romaOAListTransition);

export default Vue.extend({
    name: "RomaOa",
    props: {
        value: { // Unit
            type: Object,
        },
        height: {
            type: String,
            default: '514'
        },
        hideHeader: {
            type: Boolean,
            default: false
        },
        hideFooter: {
            type: Boolean,
            default: false
        },
        hideBadges: {
            type: Boolean,
            default: false
        },
        hideEditHour: {
            type: Boolean,
            default: false
        },
        applyRowColor: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            unit: null,
            dialog: false,
            selectedAll: false,
            dialogDeleteOA: false,
            dialogActivity: false,
            oaToDelete: null,
            mask: '##:##',
            selectedRows: [],
            expanded: [],
            buttons: [
                {
                    label: 'Cobertura',
                    icon: 'fas fa-history',
                    onClick: () => {
                        this.selectedRows.forEach(oa => {
                          if (!oa.disabled) {
                              oa.selectedRows.forEach(ie => {
                                  ie.editable = !ie.editable;
                              });
                          }
                        });
                    },
                },
                {
                    label: 'Actividades',
                    icon: 'fas fa-user-check',
                    onClick: () => {
                        this.dialogActivity = true;
                    },
                },
                {
                    label: 'Alternar activo/inactivo',
                    icon: 'fas fa-times-circle',
                    onClick: () => {
                        this.toggleOa(this.selectedRows);
                    },
                }
            ],
            oaDetails: [
                {
                    name: 'actividades',
                    icon: 'fas fa-user-check',
                    prop: 'activities',
                    color: 'secondary',
                    onClick: () => {
                        window.location.hash = 'tab-2'
                    }
                },
                {
                    name: 'comentarios',
                    icon: 'fas fa-comment',
                    prop: 'comments',
                    color: 'primary',
                    onClick: () => {}
                },
            ],
            headerOAs: [
                {
                    text: '',
                    align: 'left',
                    value: 'id',
                },
                { text: 'Nombre', value: 'name' },
                { text: 'Comentarios', value: 'comments' },
                { text: '', sortable: false },
                { text: '', value: 'data-table-expand', sortable: false },
            ],
            headerIEs: [
                { text: 'ID', value: 'id' },
                { text: 'Nombre', value: 'name' },
                {text: '', value: 'editIE'}
            ],
        };
    },
    computed: {
        isIndeterminate () {
            let opt = false;
            if (this.selectedRows.length !== this.value.oas.length && this.selectedRows.length !== 0) opt = true;
            return opt
        },
        totalSelectedIEs () {
          let sum = 0;
          if (this.selectedRows.length > 0) {
              sum = this.selectedRows.map(oa => oa.selectedRows.length);
              sum = sum.reduce((a, b) => a + b);
          }

          return sum;
        },
        totalHours () {
            let h= 0;
            let m = 0;
            if (this.value.oas) {
                const hours = this.value.oas.map(item => item.hours);
                for (let i = 0; i < hours.length; i++) {
                    let arr = hours[i].split(':');
                    h += parseInt(arr[0]);
                    m += parseInt(arr[1]);
                    h += Math.floor(m / 60) ;
                    m = m % 60;
                }
            }

            if (isNaN(h) && isNaN(m)) {
                h = "00";
                m = "00";
            }

            return h + ' Horas - ' + m + ' Minutos';
        },
    },
    watch: {
      selectedRows: {
          deep: true,
          handler (value) {
              if (value.length === 0) this.selectedAll = false;
              this.$emit('oa-selected', this.selectedRows);
          }
      }
    },
    methods: {
      selectOA ({item, value}) {
          item.selectedRows = value ? item.ies : []; // Selecciona todos los ies de un oa
      },
      selectAllOAs () {
          if (this.selectedAll) {
              this.selectedRows = this.value.oas;
              this.selectedRows.forEach(oa => {
                  oa.selectedRows = oa.ies;
              });
          } else {
              this.selectedRows.forEach(oa => {
                  oa.selectedRows = [];
              });
              this.selectedRows = [];
          }
      },
      selectIE (oa: object, {value}) {
          const index = this.selectedRows.indexOf(oa);
          if (value) {
              if (index === -1) this.selectedRows.push(oa); // agrega a oa seleccionado si se marca un ie
          } else {
              if (oa.selectedRows.length === 1) {
                  this.selectedRows.splice(index, 1); // Si deshabilita el ultimo ie quito el oa de seleccionado
              }
          }
      },
      makeEditable ({item}) {
          if (item.selectedRows.length > 0) {
              item.selectedRows.forEach(ie => {
                  ie.editable = !ie.editable;
              });
          }
      },
      toggleOa (oas) {
        if (Array.isArray(oas)) {
            oas.forEach((oa) => {
                oa.disabled = !oa.disabled;
                oa.hours = '00:00'; // Reinicio la hora para que no cuente en el total
                oa.selectedRows = [];
            });
            this.selectedRows = []; //unselect rows
        } else {
            oas.disabled = !oas.disabled;
        }
        this.$store.commit('setUnit', {unit: this.value});
      },
      goDelOA (oa) {
          this.dialogDeleteOA = true;
          this.oaToDelete = oa;
      },
      delOA () {
          const index = this.value.oas.indexOf(this.oaToDelete);
          this.value.oas.splice(index, 1);

          const data = {
              unit: this.value,
              school: this.$store.state.organizer.selectedSchool,
          };

          this.$store.commit('setUnit', data);

          this.oaToDelete = null;
          this.dialogDeleteOA = false;
      },
      rowStyle (itemIndex: number, disabled: boolean) {
          const colors = ['--v-roma_rcolor-base', '--v-primary-base', '--v-secondary-base', '--v-roma_acolor-base'];
          let color = colors[itemIndex];
          color = `var(${colors[itemIndex % colors.length]})`;

          if (disabled || !this.applyRowColor) color = 'grey';

          return `border-left: 8px solid ${color};`;
      }
    },
    components: {
        RomaCoverage,
        RomaOaModalForm,
        RomaConfirmDialog,
        RomaActivityModalForm,
    },
    directives: {
        mask,
        Ripple
    }
});
